.movies {
	display: flex;

	align-items: flex-start;
	flex-wrap: wrap;
	width: 80%;
	margin: 0 auto;
}

.movie__list {
	padding: 20px;
}

.movie__list .movie__box {
	display: none;
}

.movie__list .movie__thumb {
	width: 200px;
	height: 280px;
	position: relative;
	border-radius: 8px;
	overflow: hidden;
	background: #fff;
}

.movie__list .movie__thumb img {
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
